.go-passwordless-page .features-row{
    margin:5% 0;
    padding:0 10%;
    h1{
        font-size: 35px;
        text-align: center;
    }
    .p{
        width: 70%;
        font-size: 20px;
        margin:20px auto 55px;
        text-align: center;
    }
    .items{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .item{
        width: 30%;
        text-align: center;
        transition: 0.35s ease-in-out;
        &:hover{
            transform: scale(1.03);
            .icon{
                border-radius:76% 24% 30% 70% / 57% 63% 37% 43%;
            }
        }
    }
    .icon{
        transition: 0.35s ease-in-out;
        background: #427cff;
        color:#fff;
        width: 80px;
        height: 80px;
        margin:0 auto;
        font-size: 40px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    h6{
        font-size: 20px;
        margin:17.5px 0;
    }
    .item p{
        font-size: 18px;
    }
}

@media(max-width:1401px){
    .go-passwordless-page .features-row{
        margin:7.5% 0;
        h1{
            font-size: 27.5px;
        }
        .p{
            width: 75%;
            font-size: 16px;
            margin:15px auto 45px;
        }
        .item{
            width: 30%;
        }
        .icon{
            width: 65px;
            height:65px;
            font-size: 35px;
        }
        h6{
            font-size:16px;
            margin:15px 0;
        }
        .item p{
            font-size: 15px;
        }
    }
}

@media(max-width:900px){
    .go-passwordless-page .features-row{
       padding:0 7.5%;
        h1{
            font-size: 22.5px;
        }
        .p{
            width: 100%;
        }
        .item{
            width: 31%;
        }
    }
}

@media(max-width:900px){
    .go-passwordless-page .features-row{
        .items{
            justify-content: center;
        }
        .item{
            width: 45%;
            margin:20px 2.5%;
        }
    }
}

@media(max-width:490px){
    .go-passwordless-page .features-row{
        .item{
            width:100%;
            margin:15px 0;
        }
    }
}