.go-passwordless-page .rows .row1{
    margin:7.5% 0;
    padding:0 10%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    .info{
        width: 40%;
    }
    .images{
        width: 40%;
        position: relative;
    }
    h1{
        font-size: 35px;
        margin-bottom: 20px;
    }
    .p{
        font-size: 20px;
        margin:10px 0 20px;
    }
    .item1{
        display: flex;
        align-items: center;
        margin:25px 0;
        p{
            font-size: 18px;
            flex: 1;
        }
        &:hover{
            .icon{
                border:1px solid #427cff;
                background: #427cff;
                svg{
                    color:#fff;
                    transform: scale(1.2);
                }
            }
        }
    }
    .icon{
        svg{
            transition: 0.3s ease-in-out;
        }
        display: flex;
        align-items: center;
        justify-content: center;
        border:1px solid #ddd;
        border-radius: 50%;
        margin-right: 30px;
        width: 80px;
        height: 80px;
        font-size: 40px;
        color:#427cff;
        transition: 0.3s ease-in-out;
    }
    .link{
        margin-top: 40px;
    }
    a{
        display: flex;
        align-items: center;
        width: fit-content;
        font-size: 18px;
        background-color: #427cff;
        color:#fff;
        padding: 13px 35px;
        border-radius: 25px;
        svg{
            font-size: 22.5px;
            margin-left: 7.5px;
        }
    }
    .img1{
        content: url('../../../../assets/GoPasswordless/img1.png');
        width: 55%;
        margin: 0 auto;
        position: relative;
        z-index: 2;
    }
    .circle{
        width: 80%;
        padding-top: 80%;
        border-radius: 50%;
        background-color: #f8f8f8;
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%, -50%);
    }
    .bg{
        content: url('../../../../assets/GoPasswordless/bg.png');
        width: 100%;
    }
    .img2{
        content: url('../../../../assets/GoPasswordless/img2.png');
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%, -50%);
        width: 90%;
        border-radius: 20px;
    }
    .user-cont{
        background: #fff;
        padding:35px;
        position: absolute;
        right: 0;
        top:40%;
        border:1px solid #f8f8f8;
        border-radius: 10px;
    }
    .user{
        content: url('../../../../assets/GoPasswordless/user.png');
        width: 110px;
    }
    .img3{
        content: url('../../../../assets/GoPasswordless/img3.png');
        width: 130%;
        margin-left: -15%;
    }
}

@media(max-width:1401px){
    .go-passwordless-page .rows .row1{
        margin:7.5% 0;
        .info{
            width: 42.5%;
        }
        .images{
            width: 41%;
        }
        h1{
            font-size: 27.5px;
            margin-bottom: 15px;
        }
        .p{
            font-size: 16px;
            margin:10px 0 15px;
        }
        .item1{
            margin:20px 0;
            p{
                font-size: 15px;
            }
        }
        .icon{
            margin-right: 25px;
            width: 70px;
            height:70px;
            font-size: 27px;
        }
        .link{
            margin-top: 25px;
        }
        a{
            font-size: 15px;
            padding: 12px 30px;
            svg{
                font-size: 20px;
                margin-left: 7.5px;
            }
        }
        .user-cont{
            padding:25px;
            top:35%;
        }
        .user{
            width: 80px;
        }
    }
}

@media(max-width:1025px){
    .go-passwordless-page .rows .row1{
        margin:10% 0;
        padding:0 5%;
        .info{
            width: 50%;
        }
    }
}

@media(max-width:830px){
    .go-passwordless-page .rows .row1{
        margin:25px 0;
        padding:0 7.5%;
        .info{
            width: 420px;
            max-width: 100%;
            margin:20px auto;
        }
        .images{
            width: 420px;
            max-width: 100%;
            margin:20px auto;
        }
        h1{
            font-size: 22.5px;
            margin-bottom: 10px;
        }
        .p{
            margin:15px 0 30px;
        }
        .item1{
            margin:15px 0;
        }
        .icon{
            margin-right: 20px;
            width: 60px;
            height:60px;
            font-size: 25px;
        }
        .link{
            margin-top: 25px;
        }
        a{
            padding: 11px 22.5px;
        }
        .user-cont{
            padding:20px;
            top:35%;
        }
        .user{
            width: 70px;
        }
        .img3{
            width: 100%;
            margin:0;
        }
    }
}

@-webkit-keyframes hvr-icon-buzz {
    50% {
      -webkit-transform: translateX(3px) rotate(2deg);
      transform: translateX(3px) rotate(2deg);
    }
    100% {
      -webkit-transform: translateX(-3px) rotate(-2deg);
      transform: translateX(-3px) rotate(-2deg);
    }
}
@keyframes hvr-icon-buzz {
    50% {
      -webkit-transform: translateX(3px) rotate(2deg);
      transform: translateX(3px) rotate(2deg);
    }
    100% {
      -webkit-transform: translateX(-3px) rotate(-2deg);
      transform: translateX(-3px) rotate(-2deg);
    }
}


.go-passwordless-page .rows .row1{
    a{
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
    }
    a svg{
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
    }
   a:hover svg, a:focus svg, a:active svg{
        -webkit-animation-name: hvr-icon-buzz;
        animation-name: hvr-icon-buzz;
        -webkit-animation-duration: 0.5s;
        animation-duration: 0.5s;
        -webkit-animation-timing-function: linear;
        animation-timing-function: linear;
        -webkit-animation-iteration-count: infinite;
        animation-iteration-count: infinite;
    }
}