.go-passwordless-page{
    overflow: hidden;
    .shared-banner{
        clip-path: polygon(0 0, 100% 0, 100% 85%, 71% 100%, 0 85%);
        padding:11% 10%;
    }
}

@media(max-width:991px){
    .go-passwordless-page{
        .shared-banner{
            clip-path: polygon(0 0, 100% 0, 100% 90%, 65% 100%, 0 85%);
            padding:13.5% 10%;
        }
    }
}
@media(max-width:767px){
    .go-passwordless-page{
        .shared-banner{
            clip-path: polygon(0 0, 100% 0, 100% 95%, 65% 100%, 0 90%);
            padding:125px 7.5% 50px;
        }
    }
}